
.App {
  height: 90vh;
  width: 100%;
  display: flex;
  margin-top:10vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.imageholder{
  background-image: url('./images/worship-image.jpg');
  overlay: blue;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
.church{
  color: blue;
  text-align: center;
  font-size: 50px;
  font-weight: bolder;
}
.area{
  color: blue;
  text-align: center;
  font-size: 20px;
  text-shadow: 4px 4px 4px white;
  padding:10px;
  margin-bottom: -20px;
  font-weight: bolder;
}

.Nav{
  height: 10vh;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  justify-content: space-between;
  font-weight: bolder;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  align-items: center;
}

.Sidebar{
  width:100%;
  min-height: 90vh;
  margin-top: -5px;
  background-color: white;
  position: absolute;
  left: -250px;
  top: 10vh;
  z-index: 1;
  transition: left 0.9s ease-in;
}

.Sidebar.open{
  left: 0;
}

ul {
  margin-left: -20px;
}
ul > li {
  height: 40px;
  border-bottom: 1px solid black;
  font-weight: bold;
  text-decoration: none;
}
a {
  text-decoration: none; 
  color: black;
}

a:hover {
  color: gray; 
}

.header{
  margin: 10px;
  text-align: center;
  padding: 10px;
  font-weight: bold;
}
@media screen and (max-width:468px) {

  .church{
    color: white;
    font-size: 30px;
    font-weight: bolder;
    margin-bottom: -40px;
  }
  .area{
    color: blue;
    font-size: 30px;
    padding:10px;
    margin-bottom: -20px;
    font-weight: bolder;
  }
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

table{
  background-color: cadetblue;
}
tr > td {
  height: 40px;
  font-weight: bold;
}

tr td:first-child{
  width: 70%;
}

table tr:nth-child(even){
  background-color: gray;
}